import React from 'react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { Tabs, Tab } from 'react-bootstrap';
import OneWayForm from '../components/oneWayForm';
import RoundTripForm from '../components/roundTripForm';
import MultiLegForm from '../components/multiLegForm';
const contactUsBackgroundImage = require('../images/bg-images/falcon-50-es.jpg');

export default class IndexPage extends React.Component {
  state = { validated: false, setValidated: false };

  handleSubmit = event => {
    const form = event.currentTarget;

    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    this.setState({ validated: true });
  };

  render() {
    return (
      <Layout
        page='contactUs'
        sliderHeading='Get A Quote'
        sliderDesc=' Ask us for a Quote '
        dedicatedToSafety
        backgroundImage={contactUsBackgroundImage}
      >
        <SEO title='Contact Us' />
        <div className='contactUs'>
          <section>
            <div className='contactDetails container'>
              <p className='text-muted'>
                Let us show you how letting AJAX JETS managing your asset can
                transform your private jet owner and flying experience.
                <br />
                Please call 617-943-5387 or submit your enquiry below.
              </p>
            </div>
          </section>
          <section>
            <div className='contactForm container'>
              <Tabs defaultActiveKey='oneWay'>
                <Tab eventKey='oneWay' title='One-way'>
                  <OneWayForm />
                </Tab>
                <Tab eventKey='roundTrip' title='Round-trip'>
                  <RoundTripForm />
                </Tab>
                <Tab eventKey='multiLeg' title='Multi-leg'>
                  <MultiLegForm />
                </Tab>
              </Tabs>
            </div>
          </section>
        </div>
      </Layout>
    );
  }
}
