import React from 'react';
import { Form, Button, Row, Col } from 'react-bootstrap';
import DatePicker from 'react-datepicker';
import { time } from '../../appConfig';

import 'react-datepicker/dist/react-datepicker.css';

export default class IndexPage extends React.Component {
  state = { validated: false, setValidated: false, startDate: new Date() };

  handleSubmit = event => {
    const form = event.currentTarget;

    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    this.setState({ validated: true });
  };
  handleChange = date => {
    this.setState({
      startDate: date
    });
  };
  render() {
    const { validated } = this.state;
    return (
      <div className='container  mt-3'>
        <Form validated={validated}>
          <Form.Group controlId='exampleForm.ControlSelect1' as={Row}>
            <Form.Label className='text-muted' column sm='4'>
              Select Aircraft
            </Form.Label>
            <Col sm='8'>
              <Form.Control as='select'>
                <option>Falcon 900</option>
                <option>Falcon 50EX/50</option>
                <option>Hawker 800XP</option>
              </Form.Control>
            </Col>
          </Form.Group>

          <Form.Group controlId='fromLocation' as={Row}>
            <Form.Label className='text-muted' column sm='4'>
              From<span style={{ color: '#dc3545' }}>*</span>
            </Form.Label>
            <Col sm='8'>
              <Form.Control placeholder='From' required />
            </Col>
          </Form.Group>
          <Form.Group controlId='toLocation' as={Row}>
            <Form.Label className='text-muted' column sm='4'>
              To<span style={{ color: '#dc3545' }}>*</span>
            </Form.Label>
            <Col sm='8'>
              <Form.Control placeholder='To' required />
            </Col>
          </Form.Group>
          <Form.Group controlId='date' as={Row}>
            <Form.Label className='text-muted' column sm='4'>
              Date<span style={{ color: '#dc3545' }}>*</span>
            </Form.Label>
            <Col sm='8'>
              <DatePicker
                selected={this.state.startDate}
                onChange={this.handleChange}
                className='text-muted'
              />
            </Col>
          </Form.Group>

          <Form.Group controlId='exampleForm.ControlSelect1' as={Row}>
            <Form.Label className='text-muted' column sm='4'>
              Time<span style={{ color: '#dc3545' }}>*</span>
            </Form.Label>
            <Col sm='8'>
              <Form.Control as='select'>
                {time.map((elem, index) => (
                  <option key={index}>{elem}</option>
                ))}
              </Form.Control>
            </Col>
          </Form.Group>
          <Form.Label className='text-muted'>
            Passenger Details<span style={{ color: '#dc3545' }}>*</span>
          </Form.Label>
          <Form.Group as={Row} controlId='adults'>
            <Form.Label className='text-muted' column sm='4'>
              Adults (16+ years)
            </Form.Label>
            <Col sm='8'>
              <Form.Control placeholder='0' required />
            </Col>
          </Form.Group>

          <Form.Group as={Row} controlId='children'>
            <Form.Label className='text-muted' column sm='4'>
              Children (2-15 years)
            </Form.Label>
            <Col sm='8'>
              <Form.Control placeholder='0' required />
            </Col>
          </Form.Group>

          <Form.Group as={Row} controlId='infants'>
            <Form.Label className='text-muted' column sm='4'>
              Infants (Under 2 years)
            </Form.Label>
            <Col sm='8'>
              <Form.Control placeholder='0' required />
            </Col>
          </Form.Group>

          <Button variant='secondary' type='submit' className='sendButton'>
            Get A Quote
          </Button>
        </Form>{' '}
      </div>
    );
  }
}
